export async function savePositionOfBimObject(parentId) {
    const categoryName = "Spatial";
    const attributeName = "XYZ center";
    // const attributeName = "xyz center";
    let spatial = SpinalGraphService.getContextWithType('geographicContext');
    let spatialId = spatial[0].info.id.get();
    let rooms = await SpinalGraphService.findInContext(parentId, spatialId, node => {
        if (node.info.type.get() == "geographicRoom") {
            SpinalGraphService._addNode(node);
            return true;
        }
        else return false;
    });
    for (const rom of rooms) {
        // console.log(rom);
        let bimObjects = await SpinalGraphService.getChildren(rom.id.get(), ["hasBimObject", "hasReferenceObject.ROOM"]);
        // console.log(bimObjects);
        // console.log("next");
        for (const bimObj of bimObjects) {
            // console.log(bimObj);
            let bimObjNode = SpinalGraphService.getRealNode(bimObj.id.get());
            let position = await viewerManager.getPositionFromBimFileIdAndDbid(bimObj.bimFileId.get(), bimObj.dbid.get());
            if (position != undefined) {
                let attributeValue = position.x + ";" + position.y + ";" + position.z;
                // console.log(position);
                let attr = await AttributeService.addAttributeByCategoryName(bimObjNode, categoryName, attributeName, attributeValue);
                // console.log(attr);
            }

        }
    }
}

export async function savePositionOfHardwareContextBimObjects(parentId, contextId) {
    const categoryName = "Spatial";
    const attributeName = "XYZ center";
    // const attributeName = "xyz center";
    
    let bimObjects = await SpinalGraphService.findInContext(parentId, contextId, node => {
        if (node.info.type.get() == "BIMObject") {
            SpinalGraphService._addNode(node);
            return true;
        }
        else return false;
    });
    bimObjects.forEach(async bimObj => {
        let bimObjNode = SpinalGraphService.getRealNode(bimObj.id.get());
        SpinalGraphService._addNode(bimObjNode);

        let position = await viewerManager.getPositionFromBimFileIdAndDbid(bimObj.bimFileId.get(), bimObj.dbid.get());
        if (position != undefined) {
            let attributeValue = position.x + ";" + position.y + ";" + position.z;
            let attr = await AttributeService.addAttributeByCategoryName(bimObjNode, categoryName, attributeName, attributeValue);
        }
    });
}

