export async function visualizeHardwareContext(parentId) {
    let networkTreeStarts = await SpinalGraphService.getChildren(parentId, "hasNetworkTreeBimObject");
    let spritesData = [];
    for (let nodeModel of networkTreeStarts) {
        let node = SpinalGraphService.getRealNode(nodeModel.id.get());
        SpinalGraphService._addNode(node);
        let positionAttribute = await AttributeService.findOneAttributeInCategory(node, "Spatial", "XYZ center");
        // let positionAttribute = await AttributeService.findOneAttributeInCategory(node, "spatial", "xyz center");
        if (positionAttribute != -1) {
            let positionOfNode = positionAttribute.value.get().split(";");
            let origin = {
                x: positionOfNode[0],
                y: positionOfNode[1],
                z: positionOfNode[2]
            };
            spritesData.push({
                position: origin,
                dbid: nodeModel.dbid.get(),
                spinalModel: { nodeId: nodeModel.id.get(), spriteType: "Device" }
            });

            let children = await SpinalGraphService.getChildren(nodeModel.id.get(), "hasNetworkTreeBimObject");
            for (let child of children) {
                let childNode = SpinalGraphService.getRealNode(child.id.get());
                // ajout prise en compte namingConvention pour filtrage
                let namingConvention = await AttributeService.findOneAttributeInCategory(childNode, "default", "namingConvention");
                if (namingConvention != -1) {
                    let value = namingConvention.value.get();
                    if (value.includes("CS2_") == false && value.includes("CS1_") == false && value.includes("MR_") == false && value.includes("BDV_") == false && value.includes("PAS_") == false && value.includes("CLU_") == false && value.includes("BLG_") == false) {
                        // jusque ici
                        let posAttr = await AttributeService.findOneAttributeInCategory(childNode, "Spatial", "XYZ center");
                        // let posAttr = await AttributeService.findOneAttributeInCategory(childNode, "spatial", "xyz center");
                        if (posAttr != -1) {
                            let positionOfChild = posAttr.value.get().split(";");
                            let destination = {
                                x: positionOfChild[0],
                                y: positionOfChild[1],
                                z: positionOfChild[2]
                            };

                            let bacnetItem = await SpinalGraphService.getChildren(child.id.get(), "hasBacnetItem");
                            if (bacnetItem.length == 0) {
                                spritesData.push({
                                    position: destination,
                                    dbid: child.dbid.get(),
                                    spinalModel: { nodeId: child.id.get(), spriteType: "" }
                                });
                            }
                            else {
                                spritesData.push({
                                    position: destination,
                                    dbid: child.dbid.get(),
                                    spinalModel: { nodeId: child.id.get(), spriteType: bacnetItem[0].itemType.get() }
                                });
                            }


                            await threeManager.drawLineBetweenPositions(origin, destination, 0xfd6c9e);
                        }
                    }
                }

            }
        }



    }
    await spriteManager.createSprites(spritesData, undefined, onSpriteClickedOpenDashboardPanel);

}

function onSpriteClickedOpenDashboardPanel(event){
    if (event.dbId != 0) {
        let sprite = spriteManager.getSpriteFromDbid(event.dbId);
        // console.log(sprite);
        let realNode = SpinalGraphService.getRealNode(sprite.info.nodeId);
        SpinalGraphService._addNode(realNode);
        let selectedNode = SpinalGraphService.getInfo(realNode.getId());
        // console.log(event);
        spinalPanelManagerService.openPanel("spinal_dashboard_panel", selectedNode);
        spinalPanelManagerService.openPanel('panel-documentation', {selectedNode: selectedNode});
    }
}

export async function visualizeRepartitionningContext(table, eventOnSpriteClicked = undefined, threejsNameOfScene = "TO-BE-DEFINED") {
    // console.log(table);
    // console.log("coucou");
    let tabSprites = [];
    let tabPositions = []; // [{origin:null, destination:null}]

    for (let room of table) {
        let masters = [room.MASTER_SLAVE.MASTER_FC, room.MASTER_SLAVE.MASTER_RC];
        for (let master of masters) {
            if (master.id != null) {
                let masterNode = SpinalGraphService.getRealNode(master.id);
                // console.log(masterNode);
                let masterPosition = await AttributeService.findOneAttributeInCategory(masterNode, "Spatial", "XYZ center");
                // let masterPosition = await AttributeService.findOneAttributeInCategory(masterNode, "spatial", "xyz center");
                // console.log(masterPosition);
                if (masterPosition != -1) {
                    let masterPositionSplitted = masterPosition.value.get().split(";");
                    let masterItem = await SpinalGraphService.getChildren(master.id, "hasBacnetItem");
                    if (masterItem.length != 0) {
                        let masterItemType = masterItem[0].itemType.get();
                        tabSprites.push({
                            position: { x: masterPositionSplitted[0], y: masterPositionSplitted[1], z: masterPositionSplitted[2] },
                            dbid: master.dbid,
                            spinalModel: { nodeId: master.id, spriteType: masterItemType }
                        });

                        for (let slave of master.Slaves) {
                            let slaveNode = SpinalGraphService.getRealNode(slave.id);
                            let slavePosition = await AttributeService.findOneAttributeInCategory(slaveNode, "Spatial", "XYZ center");
                            // let slavePosition = await AttributeService.findOneAttributeInCategory(slaveNode, "spatial", "xyz center");
                            if (slavePosition != -1) {
                                let slavePositionSplitted = slavePosition.value.get().split(";");
                                let slaveItem = await SpinalGraphService.getChildren(slave.id, "hasBacnetItem");
                                if (slaveItem.length != 0) {
                                    let slaveItemType = slaveItem[0].itemType.get();
                                    tabSprites.push({
                                        position: { x: slavePositionSplitted[0], y: slavePositionSplitted[1], z: slavePositionSplitted[2] },
                                        dbid: slave.dbid,
                                        spinalModel: { nodeId: slave.id, spriteType: slaveItemType }
                                    });

                                    tabPositions.push({
                                        origin: { x: masterPositionSplitted[0], y: masterPositionSplitted[1], z: masterPositionSplitted[2] },
                                        destination: { x: slavePositionSplitted[0], y: slavePositionSplitted[1], z: slavePositionSplitted[2] }
                                    });
                                }
                            }
                        }
                    }

                }
            }

        }
    }

    for (let element of tabPositions) {
        await threeManager.drawLineBetweenPositions(element.origin, element.destination, 0x00FF00, threejsNameOfScene);
    }
    await spriteManager.createSprites(tabSprites, undefined, eventOnSpriteClicked);

}

export async function redrawMasterSlaveLines(masterSlaveTab, threejsNameOfScene = "TO-BE-DEFINED") {
    // console.log(masterSlaveTab);
    let masters = [masterSlaveTab.MASTER_FC, masterSlaveTab.MASTER_RC];
    let tabPositions = [];
    for (let master of masters) {
        if (master.id != null) {
            let masterNode = SpinalGraphService.getRealNode(master.id);
            let masterPosition = await AttributeService.findOneAttributeInCategory(masterNode, "Spatial", "XYZ center");
            // let masterPosition = await AttributeService.findOneAttributeInCategory(masterNode, "spatial", "xyz center");
            if (masterPosition != -1) {
                let masterPositionSplitted = masterPosition.value.get().split(";");
                let masterItem = await SpinalGraphService.getChildren(master.id, "hasBacnetItem");
                if (masterItem.length != 0) {
                    for (let slave of master.Slaves) {
                        let slaveNode = SpinalGraphService.getRealNode(slave.id);
                        let slavePosition = await AttributeService.findOneAttributeInCategory(slaveNode, "Spatial", "XYZ center");
                        // let slavePosition = await AttributeService.findOneAttributeInCategory(slaveNode, "spatial", "xyz center");
                        if (slavePosition != -1) {
                            let slavePositionSplitted = slavePosition.value.get().split(";");
                            let slaveItem = await SpinalGraphService.getChildren(slave.id, "hasBacnetItem");
                            if (slaveItem.length != 0) {
                                tabPositions.push({
                                    origin: { x: masterPositionSplitted[0], y: masterPositionSplitted[1], z: masterPositionSplitted[2] },
                                    destination: { x: slavePositionSplitted[0], y: slavePositionSplitted[1], z: slavePositionSplitted[2] }
                                });
                            }
                        }
                    }
                }

            }
        }

    }

    for (let element of tabPositions) {
        await threeManager.drawLineBetweenPositions(element.origin, element.destination, 0x0080FF /* 0x00FF00*/, threejsNameOfScene);
    }

}
