import { getBBoxAndMatrix } from "spinal-spatial-referential";
import { SELECTrelationList, utilities } from "../constants";
import { SpinalContext, SpinalGraphService, SpinalNode, SPINAL_RELATION_PTR_LST_TYPE } from "spinal-env-viewer-graph-service";

export async function isolateAndTopView(parentId) {

    //window.spinal.ForgeViewer.viewer.fitToView()

    this.viewer = window.spinal.ForgeViewer.viewer;
    let viewCube = await this.viewer.loadExtension('Autodesk.ViewCubeUi');
    viewCube.setViewCube("top"); // passage en vue de dessus : window.spinal.ForgeViewer.viewer.setViewCube("top");
    let self = this;
    let boolSameNode = false
    let modelResetIsolate = []
    let aggregateIsolation = []
    let realNode = SpinalGraphService.getRealNode(parentId);
    if (this.lastNode == undefined) {
        this.lastNode = realNode
    } else {
        aggregateIsolation = this.viewer.getAggregateIsolation()
        if (this.lastNode.info.id.get() == realNode.info.id.get() &&
            aggregateIsolation.length) {
            boolSameNode = true
        } else {
            this.lastNode = realNode
        }
    }
    if (boolSameNode) {
        for (let i = 0; i < aggregateIsolation.length; i++) {
            const element = aggregateIsolation[i];
            self.viewer.isolate(0, element.model)
        }
    } else {
        this.viewer = window.spinal.ForgeViewer.viewer
        realNode.find(SELECTrelationList,
            function (node) {
                if (node.info.type.get() === "BIMObject") return true;
            }).then(lst => {
                utilities.sortBIMObjectByModel(lst).then(lstByModel => {
                    for (let i = 0; i < lstByModel.length; i++) {
                        const element = lstByModel[i];
                        for (let j = 0; j < element.model.modelScene
                            .length; j++) {
                            const scene = element.model.modelScene[j];
                            if (element.dbid.length != 0) {
                                self.viewer.isolate(element.dbid, scene.model)
                            } else {
                                let rootId = scene.model.getRootId()
                                scene.model.getObjectTree((tree) => {
                                    let dbidRoot = tree.nodeAccess.dbIdToIndex[
                                        rootId]
                                    self.viewer.isolate([dbidRoot], scene.model)
                                })
                            }
                        }
                    }
                })
            });
    }

    // return this.viewer;

    // return 0;

    // this.viewer.fitToView();

}

export async function getPositionFromBimFileIdAndDbid(bimFileId, dbid) {
    let model = spinal.BimObjectService.getModelByBimfile(bimFileId);
    if (model != undefined) {
        let geom = await getBBoxAndMatrix(dbid, model, window.spinal.ForgeViewer.viewer);
        if (geom != undefined) {
            // console.log(geom);
            let position = geom.bbox.getCenter();
            return position;
        }
    }
    return undefined;

}

export function fitOn(parentId){
    this.viewer = window.spinal.ForgeViewer.viewer
    let self = this;
    let realNode = SpinalGraphService.getRealNode(parentId);
    this.viewer = window.spinal.ForgeViewer.viewer

    realNode.find(SELECTrelationList,
      function(node) {
        if (node.info.type.get() === "BIMObject") return true;
      }).then(lst => {
      utilities.sortBIMObjectByModel(lst).then(lstByModel => {
        let arrayToFit = []
        for (let i = 0; i < lstByModel.length; i++) {
          const element = lstByModel[i];
          let obj = {
            model: element.model.modelScene[0].model,
            selection: element.dbid
          }
          arrayToFit.push(obj);
          obj.model.selector.setSelection(element.dbid, obj
            .model,
            "selectOnly")
        }
        self.viewer.fitToView(arrayToFit);
      })
    });
}
