export function getSpriteFromDbid(dbId){
    let sprite = undefined;
    let index = viewableData.viewables.findIndex(elt => elt.dbId == dbId);
    if (index > -1) {
        sprite = viewableData.viewables[index];
    }
    return sprite;
}

export function getSpriteFromNodeId(nodeId) {
    let sprite = undefined;
    let index = viewableData.viewables.findIndex(elt => {
        if (elt.info != undefined) {
            return elt.info.nodeId == nodeId;
        }
        return false;
    });
    if (index > -1) {
        sprite = viewableData.viewables[index];
    }
    return sprite;
}

